.irs-line-mid,
.irs-line-left,
.irs-line-right,
.irs-bar,
.irs-bar-edge,
.irs-slider {
  background: url('../img/sprite-skin-flat.png') repeat-x;
}

.irs {
  height: 40px;
}

.irs-with-grid {
  height: 60px;
}

.irs-line {
  height: 12px;
  top: 25px;
}

.irs-line-left {
  background-position: 0 -30px;
  height: 12px;
}

.irs-line-mid {
  background-position: 0 0;
  height: 12px;
}

.irs-line-right {
  background-position: 100% -30px;
  height: 12px;
}

.irs-bar {
  background-position: 0 -60px;
  height: 12px;
  top: 25px;
}

.irs-bar-edge {
  background-position: 0 -90px;
  height: 12px;
  top: 25px;
  width: 9px;
}

.irs-shadow {
  background: #000;
  height: 3px;
  opacity: .25;
  top: 34px;
}

.lt-ie9 {
  .irs-shadow {
    filter: alpha(opacity=25);
  }
}

.irs-slider {
  background-position: 0 -120px;
  height: 18px;
  top: 22px;
  width: 16px;

  &.state_hover,
  &:hover {
    background-position: 0 -150px;
  }
}

.irs-min,
.irs-max {
  background: #eee;
  border-radius: 4px;
  color: #808080;
  font-size: 10px;
  line-height: 1.333;
  padding: 1px 3px;
  text-shadow: none;
  top: 0;
}

.irs-from,
.irs-to,
.irs-single {
  background: #e67e22;
  border-radius: 4px;
  color: #fff;
  font-size: 10px;
  line-height: 1.333;
  padding: 1px 5px;
  text-shadow: none;
}

.irs-from,
.irs-to,
.irs-single {
  &:after {
    border-top-color: #e67e22;
    border: 3px solid transparent;
    bottom: -6px;
    content: " ";
    display: block;
    height: 0;
    left: 50%;
    margin-left: -3px;
    overflow: hidden;
    position: absolute;
    width: 0;
  }
}

.irs-grid-pol {
  background: #ddd;
}

.irs-grid-text {
  color: #999;
}

.irs-disabled {}
